<template>
  <article>
    <h1> Select</h1>

    <h2></h2>

    <r-row>
      <r-col xs6>
        <r-select v-model="seled1" :options="options" id="test" label="Browser Select" dftDisabled name="test"  :dftTxt="dft"></r-select>
      </r-col>
    </r-row>
    <p v-if="seled1">
       {{seled1}}
    </p>

    <h3></h3>
    <r-row>
    <r-col xs6>
      <r-select v-model="seled2" :options="options" id="test" label="FF " name="test"  :dftTxt="dft"></r-select>
    </r-col>
    </r-row>

    <h3>Disabled Option</h3>
    <r-row>
    <r-col xs6>
      <r-select v-model="seled2" :options="options2" id="test" label="disabled option" name="test"  :dftTxt="dft"></r-select>
    </r-col>
    </r-row>
    
    
  </article>
</template>

<script>
export default {
  name: 'selects',
  data(){
    return {
      dft: '',
      seled1: null,
      seled2: 'ff',
      options: [
        {
          value: 'chrome',
          text: 'Chrome',
        },
        {
          value: 'safari',
          text: 'Safari',
        },
        {
          value: 'ff',
          text: 'FireFox',
        }
      ],
      options2: [
        {
          value: 'chrome',
          text: 'Chrome',
        },
        {
          value: 'disabled',
          text: 'Disabled: ',
          disabled: true
        },
        {
          value: 'disabled',
          text: 'Disabled:  +1',
          disabled: true
        },
        {
          value: 'ff',
          text: 'FireFox',
        }
      ],
    }
  }
}
</script>



